.header {
  position: relative;
  background-image: url("/images/header.jpg");
  background-size: cover;
  background-position: center;
  height: 700px;
  padding: 56px 120px 48px;
  box-sizing: border-box;

  &__nav {
    position: absolute;
    bottom: 48px;
    left: 0;
    right: 0;
  }

  &__top {
    display: flex;
    top: 56px;
    justify-content: space-between;
    color: #fff;
  }

  &__phone {
    color: #fff;
    text-decoration: none;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__link {
    color: #fff;
    text-decoration: none;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding-bottom: 8px;
    border-bottom: 1px solid;
    @include hover(color, $hover-color);
  }

  &__title {
    position: absolute;
    bottom: 165px;
    margin: 0;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 60px;
    line-height: 60px;
    color: #fff;
  }

  &__title:before {
    content: "";
    position: absolute;
    top: -16px;
    left: 0;
    width: 35px;
    height: 2px;
    background-color: #fff;
  }

}
